<script>
export default {
  name: "articleHomeItem",
  data() {
    return {
      id: this.articleList.article_id,
      article_status: this.articleList.article_status,
      article_link: this.articleList.article_link,
      article_title: this.articleList.article_title,
      article_major: this.articleList.article_major,
      article_labels: this.articleList.article_labels,
      article_introduce: this.articleList.article_introduce,
      article_uploaddate: this.articleList.article_uploaddate,
      article_updatedate: this.articleList.article_updatedate,
      author_id: this.articleList.author_id,
      author_head: this.articleList.author_headphoto,
      author_name: this.articleList.author_name,
      author_signature: this.articleList.author_signature,
      author_university: this.articleList.author_university,
      article_num: this.articleList.article_num,
      like_num: this.articleList.like_num,
      collection_num: this.articleList.collection_num,
      share_num: this.articleList.share_num,
      comment_num: this.articleList.comment_num,
    };
  },
  computed: {
    major() {
      return this.article_major.join("-");
    },
  },
  methods: {
    push(num) {
      if (num == 1) {
        const routeUrl = this.$router.resolve({
          path: "/articleHome/" + this.id,
        });
        window.open(routeUrl.href, "_blank");
      }
      if (num == 2) {
        this.$router.push({
          path: "/personalCenter/" + this.author_id,
        });
      }
    },
  },
  watch: {
    articleList: {
      handler: function (newV, _) {
        this.id = newV.article_id;
        this.article_status = newV.article_status;
        this.article_link = newV.article_link;
        this.article_title = newV.article_title;
        this.article_major = newV.article_major;
        this.article_labels = newV.article_labels;
        this.article_introduce = newV.article_introduce;
        this.article_uploaddate = newV.article_uploaddate;
        this.article_updatedate = newV.article_updatedate;
        this.author_head = newV.author_headphoto;
        this.author_name = newV.author_name;
        this.author_signature = newV.author_signature;
        this.author_university = newV.author_university;
        this.like_num = newV.like_num;
        this.collection_num = newV.collection_num;
        this.share_num = newV.share_num;
        this.comment_num = newV.comment_num;
        this.article_num = newV.article_num;
      },
    },
    immediate: true,
    deep: true,
  },
  props: ["articleList"],
};
</script>

