<template>
  <div name="ArticlePost" class="ArticlePost-wrap">
    <el-row type="flex" justify="center">
      <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
      >
        <el-menu-item style="cursor: default"
          >课程选择：<el-divider direction="vertical"></el-divider
        ></el-menu-item>
        <!-- 具体课程的大类选择 -->
        <el-menu-item index="1">全部</el-menu-item>
        <el-menu-item index="2">文学</el-menu-item>
        <el-menu-item index="3">理学</el-menu-item>
        <el-menu-item index="4">工学</el-menu-item>
        <el-menu-item index="5">艺术学</el-menu-item>
        <el-menu-item index="6">农学</el-menu-item>
        <el-menu-item index="7">其他学科</el-menu-item>

        <el-menu-item v-if="searchchoicenum != 0">
          <el-cascader
            v-if="searchchoicenum == 1"
            v-model="searchchoice"
            placeholder="请选择具体类别"
            :options="options1"
            :props="{ expandTrigger: 'hover' }"
            style="width: 200px"
            :value="value"
            @change="searchitem(searchchoice)"
          ></el-cascader>

          <el-cascader
            v-if="searchchoicenum == 2"
            v-model="searchchoice"
            placeholder="请选择具体类别"
            :options="options2"
            :props="{ expandTrigger: 'hover' }"
            style="width: 200px"
            :value="value"
            @change="searchitem(searchchoice)"
          ></el-cascader>

          <el-cascader
            v-if="searchchoicenum == 3"
            v-model="searchchoice"
            placeholder="请选择具体类别"
            :options="options3"
            :props="{ expandTrigger: 'hover' }"
            style="width: 200px"
            :value="value"
            @change="searchitem(searchchoice)"
          ></el-cascader>

          <el-cascader
            v-if="searchchoicenum == 4"
            v-model="searchchoice"
            placeholder="请选择具体类别"
            :options="options4"
            :props="{ expandTrigger: 'hover' }"
            style="width: 200px"
            :value="value"
            @change="searchitem(searchchoice)"
          ></el-cascader>

          <el-cascader
            v-if="searchchoicenum == 5"
            v-model="searchchoice"
            placeholder="请选择具体类别"
            :options="options5"
            :props="{ expandTrigger: 'hover' }"
            style="width: 200px"
            :value="value"
            @change="searchitem(searchchoice)"
          ></el-cascader>
        </el-menu-item>

        <el-menu-item style="cursor: default; position: absolute; right: 0px">
          <el-input
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            v-model="keywords"
            style="width: 200px"
          >
          </el-input>
          <el-button style="margin-top: 3px" @click="search(keywords)"
            >搜索</el-button
          >
        </el-menu-item>
      </el-menu>
    </el-row>

    <el-row style="margin-top: 30px">
      <el-row>
        <div style="margin: 0 0 0 20px">
          <font class="title">我发布的文章({{ post_article_num }})</font>
        </div>
      </el-row>
      <el-row
        type="flex"
        justify="center"
        style="margin: 30px 0 0 0; flex-wrap: wrap"
      >
        <article_personalcenter_item
          v-for="(item, index) in article_list"
          :key="index"
          :articleList="item"
        />
        <!-- 占位区域 -->
        <div
          style="width: 502px; margin: 0 60px 30px 0; padding: 9px 14px"
          v-for="item in new Array(2 - (((article_list.length - 1) % 2) + 1))"
          :key="item"
        ></div>
      </el-row>
    </el-row>
  </div>
</template>

<script>
import article_personalcenter_item from "@/components/little/article_personalcenter_item.vue";
import { getArticleListAPI } from "@/api/articles";
import { Loading } from "element-ui";
export default {
  name: "ArticlePost",
  data() {
    return {
      keywords: "",
      value: [],
      activeIndex: "1",
      searchchoicenum: 0,
      searchchoice: [],
      article_list: [],
      article_list_all: [],
      options1: [
        {
          value: "中国语言文学类",
          label: "中国语言文学类",
        },
        {
          value: "新闻传播学类",
          label: "新闻传播学类",
        },
        {
          value: "外国语言文学类",
          label: "外国语言文学类",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options2: [
        {
          value: "信息与计算科学",
          label: "信息与计算科学",
        },
        {
          value: "数学与应用数学",
          label: "数学与应用数学",
        },
        {
          value: "应用化学",
          label: "应用化学",
        },
        {
          value: "生物技术",
          label: "生物技术",
        },
        {
          value: "生物科学",
          label: "生物科学",
        },
        {
          value: "应用物理学",
          label: "应用物理学",
        },
        {
          value: "应用心理学",
          label: "应用心理学",
        },
        {
          value: "统计学",
          label: "统计学",
        },
        {
          value: "化学",
          label: "化学",
        },
        {
          value: "物理学",
          label: "物理学",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options3: [
        {
          value: "土木工程",
          label: "土木工程",
        },
        {
          value: "机械类专业",
          label: "机械类专业",
        },
        {
          value: "电气工程及其智能化",
          label: "电气工程及其智能化",
        },
        {
          value: "计算机科学与技术",
          label: "计算机科学与技术",
        },
        {
          value: "车辆工程",
          label: "车辆工程",
        },
        {
          value: "软件工程",
          label: "软件工程",
        },
        {
          value: "交通运输工程",
          label: "交通运输工程",
        },
        {
          value: "自动化",
          label: "自动化",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options4: [
        {
          value: "音乐",
          label: "音乐",
        },
        {
          value: "舞蹈",
          label: "舞蹈",
        },
        {
          value: "影视学类",
          label: "影视学类",
        },
        {
          value: "戏剧学类",
          label: "戏剧学类",
        },
        {
          value: "美术学类",
          label: "美术学类",
        },
        {
          value: "设计学类",
          label: "设计学类",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options5: [
        {
          value: "园艺",
          label: "园艺",
        },
        {
          value: "种子科学与工程",
          label: "种子科学与工程",
        },
        {
          value: "设施农业科学与工程",
          label: "设施农业科学与工程",
        },
        {
          value: "农业资源与环境",
          label: "农业资源与环境",
        },
        {
          value: "动物科学",
          label: "动物科学",
        },
        {
          value: "林学",
          label: "林学",
        },
        {
          value: "水产养殖学",
          label: "水产养殖学",
        },
        {
          value: "草业学科",
          label: "草业学科",
        },
        {
          value: "茶学",
          label: "茶学",
        },
        {
          value: "烟草",
          label: "烟草",
        },
        {
          value: "农艺教育",
          label: "农艺教育",
        },
        {
          value: "园艺教育",
          label: "园艺教育",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
    };
  },
  computed: {
    post_article_num() {
      return this.article_list_all.length;
    },
  },
  methods: {
    searchitem(value) {
      value = value.join("/");
      console.log(value);
      this.article_list = [];
      this.article_list_all.forEach((item) => {
        if (item.article_major.indexOf(value) != -1) {
          this.article_list.push(item);
        }
      });
    },
    handleSelect(key) {
      if (key == 1) {
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          this.article_list.push(item);
        });
      }
      if (key == 2) {
        this.article_list = [];
        this.searchchoicenum = 1;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("文学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 3) {
        this.article_list = [];
        this.searchchoicenum = 2;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("理学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 4) {
        this.article_list = [];
        this.searchchoicenum = 3;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("工学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 5) {
        this.article_list = [];
        this.searchchoicenum = 4;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("艺术学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 6) {
        this.article_list = [];
        this.searchchoicenum = 5;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("农学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 7) {
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("其他学科") != -1) {
            this.article_list.push(item);
          }
        });
      }
    },
  },
  mounted() {
    let loading;
    //定义加载动画
    function startLoading() {
      // Loading.service(options); options 参数为 Loading 的配置项
      // 使用loading变量来接收Loading.service返回的实例
      loading = Loading.service({
        lock: true, //是否锁定
        text: "少女祈祷中...", //显示在加载图标下方的加载文案
        background: "rgba(0,0,0,0.4)", //遮罩背景色
      });
    }
    //结束加载动画，调用该实例的 close 方法来关闭
    function endLoading() {
      loading.close();
    }

    startLoading();
    getArticleListAPI().then((res) => {
      if (res.data.article_list) {
        res.data.article_list.forEach((item) => {
          if (item.author_id == this.$route.params.id) {
            this.article_list_all.push(item);
          }
        });
        this.article_list_all.reverse();
        this.article_list = this.article_list_all;
      }
    });
    endLoading();
  },
  components: { article_personalcenter_item },
};
</script>

<style scoped>
.ArticlePost-wrap {
  position: relative;
  width: 100%;
  background: #ffffff;
  border-radius: 10px;
  padding: 20px;
}
.title {
  font-family: SourceHanSansCN-Regular;
  font-size: 24px;
  font-weight: normal;
  color: #3d3d3d;
}
.el-menu {
  width: 100%;
  background-color: #ffffff !important;
  border-radius: 10px;
  overflow: hidden;
  border: 0.5px solid #00ead8;
}
.el-menu-item:hover {
  color: #00ead8 !important;
}
.el-menu-item.is-active {
  color: #00ead8 !important;
}
</style>